.Carusel {
    display: grid;
    grid-template-areas: 'button_left carusel_window button_right'
        'indicator_nav indicator_nav indicator_nav';
        grid-template-columns: max-content 1fr max-content;
    align-items: center;

    .button {

        :hover {
            cursor: pointer;

            circle {
                fill: #84C551;
            }

            path {
                fill: #fff;
            }
        }
    }

    .button_left {
        grid-area: button_left;
        // background-color: red;
        justify-self: start;
        padding-left: 10px;
    }

    .button_right {
        grid-area: button_right;
        justify-self: end;
        padding-right: 10px;
        // background-color: green;

        svg {
            transform: scaleX(-1);
        }
    }

    .carusel_window {
        grid-area: carusel_window;
        justify-self: center;
        width: 100%;
        max-width: 1140px;
        overflow: hidden;

        .ribbon {
            position: relative;
            width: max-content;
            display: flex;

            * {
                white-space: normal;
            }

            &>* {
                margin-right: 15px;
                margin-left: 15px;
            }
        }

    }

    .indicator_nav {
        display: flex;
        align-items: end;
        grid-area: indicator_nav;
        justify-self: center;
        margin-top: 40px;
        // background-color: yellow;

        &>* {
            display: block;
            width: 6px;
            border-radius: 20px;
        }

        .shortLine {
            height: 16px;
            background: #8D32001A;

            &:nth-child(n +2) {
                margin-left: 5px;
            }

            &.active {
                height: 30px;
                background: #8D3200;
            }
        }


    }
}