.plant_protection {
    margin-top: 10px;
    margin-bottom: 120px;
    padding: 0 15px;
    font-size: 50px;
    background-color: white;

    @media (max-width:1200px) {
        font-size: 22px;
    }

    .block__title {
        text-align: center;
    }

    .catalog_galary {
        margin-top: 35px;
        display: flex;
        justify-content: space-evenly;
        flex-wrap: wrap;

        .catalog_galary__item {
            margin:15px;
            position: relative;

            img {
                width: 255px;
                height: 165px;
            }

            span {
                display: block;
                padding:10px 0;
                position: absolute;
                bottom: 0;
                width: 100%;
                background-color: rgba(255, 255, 255, 0.705);
                border-radius: 18px;
                font-weight: 600;
                font-size: 22px;
                text-align: center;
            }
        }
    }
}