@import "mini_component/card.scss";

.novelties_carusel {
    padding-top: 110px;
    padding-bottom: 120px;
    background-color: grey;
    background-image: url("../img/block_element/novelties_carusel.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;

    .text_block__title {
        margin-bottom: 40px;
        font-size: 45px;
        font-weight: 700;
        line-height: 54px;
        text-align: center;
        color:white;
    }

    @import "mini_component/Carusel.scss";

    .Carusel .indicator_nav {
        .shortLine {
            background: rgba(255, 255, 255, 0.2);

            &.active {
                background: white;
            }
        }
    }

    .main_button {
        margin: 40px auto 0;
    }
}