@mixin flexCC () {
    display: flex;
    justify-content: center;
    align-items: center;
}
@mixin flexBC () {
    display: flex;
    justify-content:space-between;
    align-items: center;
}
@mixin flexEC () {
    display: flex;
    justify-content: flex-end;
    align-items: center;
}