._button_template {
    @include flexCC();
    width: 220px;
    height: 45px;
    border-radius: 90px;
    font-weight: 700;
    line-height: 20px;
    color: white;
    background-color: $color_OrangeRed;

}

._leaf_title_decor {
    position: relative;

    &::before {
        content: "";
        display: inline-block;
        margin-right: 10px;
        width: 32px;
        height: 25px;
        background-image: url("../img/icon/icon_leaves_vector/leaves_green_horisontal.png");
        background-size: 100% 100%;
    }
}

._double_leaf_title_decor {
    position: relative;

    &::before {
        content: "";
        display: inline-block;
        margin-right: 10px;
        width: 32px;
        height: 25px;
        background-image: url("../img/icon/icon_leaves_vector/leaves_green_horisontal.png");
        background-size: 100% 100%;

        @media(max-width:1200px) {
            width: 14px;
            height: 11px;
        }
    }

    &::after {
        content: "";
        display: inline-block;
        margin-left: 10px;
        width: 32px;
        height: 25px;
        background-image: url("../img/icon/icon_leaves_vector/leaves_green_horisontal.png");
        background-size: 100% 100%;
        transform: scaleX(-1);
        @media(max-width:1200px) {
                width: 14px;
                height: 11px;
            }
    }
}


._text_block_template {

    .text_block__title {
        font-size: 55px;
        font-weight: 700;
        line-height: 67px;

        @media(max-width: 1200px) {
            font-size: 35px;
            line-height: 45px;
        }

        .thin {
            font-size: 45px;
            font-weight: 300;

            @media(max-width: 1200px) {
                font-size: 25px;
            }
        }

    }

    .text_block__text {
        line-height: 22px;
        font-size: 18px;
        color: rgba(0, 0, 0, 0.5);
    }

    .text_block__link {
        margin-top: 25px;
    }
}