.main_section {
    position: relative;
    margin-top: -160px;
    z-index: -1;
    padding-top: 160px;
    padding-bottom: 270px;
    background-color: white;
    background-image:
        linear-gradient(to right, rgba(255, 255, 255, 0.72), rgb(255, 255, 255) 1300px),
        url("../img/bgc/сбор помидор.jpg");
    background-position: left top;
    background-repeat: no-repeat;
    background-size: auto 100%;

    @media(max-width: 1200px) {
        padding-top: 30px;
        padding-bottom: 12vw;
    }

    ._conteiner {
        &>.button {
            display: none;
        }

        @media(max-width: 1200px) {
            display: flex;
            flex-direction: column;
            align-items: center;

            &>.button {
                display: flex;
                margin-top: 3vw;
                width: 90%;
                height: 7vw;
                min-height: 50px;
            }
        }

    }

    .text_block {
        position: relative;
        margin-top: 180px;
        width: 605px;
        padding-left: 46px;
        z-index: 2;

        @media(max-width: 1200px) {
            width: 90vw;
        }


        ._leaf_title_decor::before {
            position: absolute;
            bottom: 12px;
            left: -38px;
        }

        .text_block__link {
            @media(max-width: 1200px) {
                display: none;
            }
        }

    }

    .fermer_frame {
        position: absolute;
        bottom: 0;
        right: 0;

        @media(max-width: 1200px) {
            position: relative;
            margin-top: 5vw;
            width: 100%;
            padding: 30px 20px;

            img {
                width: 100%;
            }
        }

        .leafs {
            position: absolute;
            top: 0;
            width: 100%;
            height: 100%;

            @media(max-width: 1200px) {
                display: none;
            }

            .leaf_0 {
                position: absolute;
                top: -35px;
                right: 15px;
            }

            .leaf_1 {
                position: absolute;
                bottom: 140px;
                left: -145px;
            }

            .leaf_2 {
                position: absolute;
                bottom: 90px;
                left: -55px;
            }
        }
    }
}