.Card_product {
    position: relative;
    display: block;
    width: 255px;
    height: 355px;
    background-color: white;
    border-radius: 20px;

    &:hover {
        cursor: pointer;
    }

    .icon_product {
        display: block;
        width: 100px;
        height: 135px;
        margin: 30px auto;
    }

    .shield {
        position: absolute;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        inset: auto 10px 10px;
        padding: 15px;
        height: 150px;
        background-color: rgba(140, 50, 19, 0.06);
        border-radius: 20px;

        .product_name {
            font-size: 16px;
            font-weight: 600;

        }

        .availability {
            font-size: 14px;

            &.true {
                color: $color_DarkGreen;
            }

            &.false {
                color: red;
            }
        }

        .price {
            font-size: 18px;
            font-weight: 700;
            color: #84BE51;
        }

        .quantity {
            font-size: 14px;
        }

        .basket {
            position: absolute;
            display: flex;
            right: 20px;
            bottom: 20px;

            &:hover,
            &.active {
                box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.15);
                border-radius: 90px;

                circle {
                    fill: #8C3213;
                }

                path {
                    fill: white;
                }
            }
        }
    }

    .comparison {
        display: flex;
        position: absolute;
        top: 20px;
        right: 20px;
        padding: 5px;
        border-radius: 50%;

        &:hover,
        &.active {
            background-color: #8C3213;

            path {
                fill: white;
            }
        }

    }

}