.main_nav {
    position: relative;
    width: 100%;
    margin-top: 20px;
    z-index: 100;

    .top {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin: 0 20px;

        .wrapper_1 {
            @include flexBC ();
        }

        form {
            @media(max-width: 1200px) {
                display: none;
            }

            $color_border: rgba(0, 0, 0, 0.2);
            display: flex;
            align-items: center;
            border: 1px solid $color_border;
            margin-left: 30px;
            padding: 0 0 0 20px;
            height: 40px;
            width: 342px;
            border-radius: 20px;
            overflow: hidden;
            background-color: #fff;

            input[type="text"] {
                line-height: 40px;
                width: 290px;
                padding: 0;
                border: 0;
                font-size: 16px;
                font-family: Montserrat;

                &:focus {
                    outline: 0;
                }

                &::placeholder {
                    font-size: 16px;
                    line-height: 40px;
                }


            }

            .vertical_line {
                height: 70%;
                width: 1px;
                background-color: $color_border;
            }

            input[type="submit"] {
                display: inline-block;
                width: 45px;
                height: 100%;
                background-color: #fff;
                background-image: url("../img/icon/nav/serch.svg");
                background-size: 15px 15px;
                background-repeat: no-repeat;
                background-position: center center;
                border: 0;
                cursor: pointer;
            }
        }

        .phone_contacts {
            border-radius: 20px 20px 0 0;
            padding: 10px;
            padding-left: 56px;
            background-color: transparent;
            background-image: url("../img/icon/icon round buttons/handset.svg");
            background-position: 10px 10px;
            background-repeat: no-repeat;

            @media(max-width: 1200px) {
                display: none;
            }

            &:hover {
                box-shadow: 0px -2px 8px rgba(0, 0, 0, 0.15);
                background-color: white;
            }

            &:hover ul {
                background-color: white;
                box-shadow: 0px 8px 8px rgba(0, 0, 0, 0.15);
            }

            ul {
                padding-left: inherit;
                width: 100%;
                background-color: transparent;
                transition: 0;

                >:first-child {
                    margin-top: 8px;
                }

                >:nth-child(n + 2) {
                    margin-top: 13px;
                }

                >:last-child {
                    margin-bottom: 0;
                }
            }


            .wrapper {
                display: flex;
                flex-direction: column;

                a {
                    margin-top: 3px;
                    color: $color_YellowGreen;
                    text-decoration: underline;
                }
            }
        }

        .wrapper_2 {
            display: flex;


            &>:first-child {
                @media(max-width: 1200px) {
                    display: none;
                }
            }

            a {
                position: relative;
                display: flex;
                align-items: center;

                >.wrapper {
                    position: relative;
                }

                .index {
                    display: flex;
                    position: absolute;
                    top: -3px;
                    left: 25px;
                    padding: 0 7px;
                    line-height: 20px;
                    text-align: center;
                    background-color: $color_YellowGreen;
                    border-radius: 10px;
                    font-size: 10px;
                    color: white;

                    &:empty {
                        display: none;
                    }
                }

                &:nth-child(n +2) {
                    margin-left: 15px;
                }

                >span.wrapper {
                    margin-left: 7px;
                    font-size: 12px;
                    font-weight: 800;
                    color: black;
                }

                &:hover svg {
                    fill: #8C3213;
                    box-shadow: 0px 5px 14px rgba(140, 50, 19, 0.4);
                    border-radius: 50%;

                    path {
                        fill: #fff;
                    }

                    &.icon_heart {
                        path {
                            stroke: #fff;
                        }
                    }
                }
            }
        }
    }

    .bottom {
        margin-top: 22px;

        @media(max-width: 1200px) {
            // margin-bottom: 50px;
        }

        .nav {
            @include flexBC();
            .wrapper{
                width: 100%;
                display: flex;
                justify-content: space-evenly;
            }
            @media(max-width: 1200px) {
                display: block;
                // background-color: grey;
                margin: 0 20px;
                width: 90vw;

                .wrapper {
                    display: none;
                }

                &:hover {
                    .wrapper {
                        position: absolute;
                        display: flex;
                        flex-direction: column;
                        .nav__item{
                            width: 90vw;
                        }
                    }

                }
            }

            .nav__item {

                a {
                    @include flexCC();
                    width: 200px;
                    box-sizing: border-box;
                    padding: 0 15px;
                    height: 40px;
                    text-transform: uppercase;
                    font-weight: 600;
                    font-size: 14px;
                    border: 1px solid $color_DarkGreen;
                    border-radius: 20px;
                    background-color: rgba(255, 255, 255, 0.798);
                    color: black;

                    @media(max-width: 1200px) {
                        margin: 0 auto;
                        width: 100%;
                        height: 50px;
                        background-color: white;


                    }

                    &:hover {
                        background-color: #e84e0f;
                        color: white;
                        border-color: #e84e0f;

                        img {
                            filter: brightness(1000%)
                        }
                    }

                    img {
                        width: 24px;
                        height: 24px;
                        margin-right: 10px;


                    }
                }

                &.selected a {
                    background-color: $color_OrangeRed;
                    border-color: $color_OrangeRed;
                    color: white;

                    img {
                        filter: brightness(1000%);
                    }

                }
            }

        }
    }


}