.footer {
    padding: 120px 0 0;

    .wrapper {
        padding: 0 20px;
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        margin-bottom: 80px;

        &>ul {
            max-width: 270px;
            margin-bottom: 25px;
            line-height: 22px;

            &>* {
                margin-bottom: 10px;
            }

            &>:first-child {
                margin-bottom: 25px;
            }
        }
    }

    .strong,
    .strong a {
        font-weight: 600;
    }

    .line {
        border: 1px solid rgba(0, 0, 0, 0.2);
    }

    .footer_bottom {
        padding: 15px 0;
        text-align: center;
        font-size: 14px;
        opacity: 0.5;
    }

    .phone_contacts {

        border-radius: 20px 20px 0 0;
        padding: 10px;
        padding-left: 36px;
        background-color: transparent;
        background-image: url("../img/icon/icon round buttons/handset.svg");
        background-position: 0px 10px;
        background-repeat: no-repeat;

        &:hover {
            box-shadow: 0px -2px 8px rgba(0, 0, 0, 0.15);
            background-color: white;
        }

        &:hover ul {
            background-color: white;
            box-shadow: 0px 8px 8px rgba(0, 0, 0, 0.15);
        }

        ul {
            padding-left: inherit;
            width: 100%;
            background-color: transparent;
            transition: 0;

            >:first-child {
                margin-top: 8px;
            }

            >:nth-child(n + 2) {
                margin-top: 13px;
            }

            >:last-child {
                margin-bottom: 0;
            }
        }


        .wrapper {
            display: flex;
            flex-direction: column;
            margin: 0;

            a {
                margin-top: 3px;
                color: $color_YellowGreen;
                text-decoration: underline;
            }
        }
    }

    .mail {
        a {
            display: flex;
            align-items: center;
            :last-child{
                margin-left: 10px;
            }
        }

        &:hover {
            circle {
                fill: #8C3213;
            }

            path {
                fill: white;
                stroke: white;
            }
        }
    }
}