section.menu {
    &.hidden {
        display: none;
    }

    position: fixed;
    top: 0px;
    z-index: 150;
    padding: 50px 15px;
    padding-right: 30px;
    background-color: $color_DarkGreen;
    box-sizing: border-box;
    height: 100vh;
    width: 100vw;
    // color: white;

    @media(min-width:1200px) {
        display: none;
    }

    .menu_top {
        span {
            color: white;
        }

        .top_wrapper {
            margin-top: 20px;
            display: flex;
            justify-content: space-between;

            .logo {
                img {
                    width: 105px;
                }
            }

            .wrapper {
                display: flex;

                a {
                    position: relative;
                    display: flex;
                    align-items: center;

                    >.wrapper {
                        position: relative;
                    }

                    .index {
                        display: flex;
                        position: absolute;
                        top: -3px;
                        left: 25px;
                        padding: 0 7px;
                        line-height: 20px;
                        text-align: center;
                        background-color: $color_YellowGreen;
                        border-radius: 10px;
                        font-size: 10px;
                        color: white;

                        &:empty {
                            display: none;
                        }
                    }

                    &:nth-child(n +2) {
                        margin-left: 15px;
                    }

                    >span.wrapper {
                        margin-left: 7px;
                        font-size: 12px;
                        font-weight: 800;
                        color: black;
                    }

                    svg {
                        fill: transparent;
                        box-shadow: 0px 5px 14px rgba(140, 50, 19, 0.4);
                        border-radius: 50%;

                        path {
                            fill: #fff;
                        }

                        circle {
                            stroke: white;
                        }

                        &.icon_heart {
                            path {
                                stroke: #fff;
                            }
                        }
                    }

                    &:hover svg {
                        fill: #8C3213;
                        box-shadow: 0px 5px 14px rgba(140, 50, 19, 0.4);
                        border-radius: 50%;

                        circle {
                            stroke: #8C3213;
                        }

                        path {
                            fill: #fff;
                        }

                        &.icon_heart {
                            path {
                                stroke: #fff;
                            }
                        }
                    }
                }
            }
        }

        .center_wrapper {
            display: flex;
            margin-top: 20px;

            .icon__link {
                svg {
                    fill: transparent;
                    box-shadow: 0px 5px 14px rgba(140, 50, 19, 0.4);
                    border-radius: 50%;

                    path {
                        fill: #fff;
                    }

                    circle {
                        stroke: white;
                    }

                    &.icon_heart {
                        path {
                            stroke: #fff;
                        }
                    }
                }

                &:hover svg {
                    fill: #8C3213;
                    box-shadow: 0px 5px 14px rgba(140, 50, 19, 0.4);
                    border-radius: 50%;

                    circle {
                        stroke: #8C3213;
                    }

                    path {
                        fill: #fff;
                    }

                    &.icon_heart {
                        path {
                            stroke: #fff;
                        }
                    }
                }
            }

            form {
                flex-grow: 1;
                $color_border: rgba(0, 0, 0, 0.2);
                display: flex;
                align-items: center;
                border: 1px solid $color_border;
                margin-left: 30px;
                padding: 0 0 0 20px;
                height: 40px;
                width: 342px;
                border-radius: 20px;
                overflow: hidden;
                background-color: #fff;

                input[type="text"] {
                    flex-grow: 1;
                    line-height: 40px;
                    min-width: 80px;
                    padding: 0;
                    border: 0;
                    font-size: 16px;
                    font-family: Montserrat;

                    &:focus {
                        outline: 0;
                    }

                    &::placeholder {
                        font-size: 16px;
                        line-height: 40px;
                    }


                }

                .wrapper {
                    height: 100%;
                    display: flex;
                    align-items: center;

                    .vertical_line {
                        height: 70%;
                        width: 1px;
                        background-color: $color_border;
                    }

                    input[type="submit"] {
                        display: inline-block;
                        width: 45px;
                        height: 100%;
                        background-color: #fff;
                        background-image: url("../img/icon/nav/serch.svg");
                        background-size: 15px 15px;
                        background-repeat: no-repeat;
                        background-position: center center;
                        border: 0;
                        cursor: pointer;
                    }
                }
            }
        }

        .bottom_wrapper {
            margin-top: 20px;

            .phone_contacts {
                border-radius: 20px 20px 0 0;
                margin-left: -10px;
                padding: 10px;
                padding-left: 56px;
                background-color: transparent;
                background-image: url("../img/icon/icon round buttons/handset_white.svg");
                background-position: 10px 10px;
                background-repeat: no-repeat;
                color: white;

                &:hover {
                    box-shadow: 0px -2px 8px rgba(0, 0, 0, 0.15);
                    background-color: white;
                    background-image: url("../img/icon/icon round buttons/handset.svg");

                    span {
                        color: black;
                    }
                }

                &:hover ul {
                    background-color: white;
                    box-shadow: 0px 8px 8px rgba(0, 0, 0, 0.15);
                }

                ul {
                    padding-left: inherit;
                    width: 100%;
                    background-color: transparent;
                    transition: 0;

                    >:first-child {
                        margin-top: 8px;
                    }

                    >:nth-child(n + 2) {
                        margin-top: 13px;
                    }

                    >:last-child {
                        margin-bottom: 0;
                    }
                }


                .wrapper {
                    display: flex;
                    flex-direction: column;

                    a {
                        margin-top: 3px;
                        color: $color_YellowGreen;
                        text-decoration: underline;
                    }
                }
            }
        }
    }

    .menu_bottom {
        margin-top: 20px;
        padding-top: 60px;
        border-top: 1px solid rgba(255, 255, 255, 0.1);

        a {
            display: block;
            color: white;
            line-height: 60px;
            font-weight: 300;
            font-size: 22px;
            text-align: center;
        }

    }
}