.information_block {
    padding: 110px 0 120px;
    color: white;
    background-color: green;
    background-image: url("../img/block_element/fermer_and_corn.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;

    ._conteiner{
        padding-left: 20px;
        padding-right: 20px;
    }
    .text_block__title {
        font-size: 45px;
        font-weight: 700;
    }

    .text_block__wrapper {
        margin-top: 15px;
        line-height: 20px;
        font-size: 16px;
    }
}