$color_header_bgc: #1E6140;
$color_header_bgc_select: #347153;


header {
    position: sticky;
    top: 0px;
    width: 100%;
    height: 50px;
    background-color: $color_header_bgc;
    color: white;
    z-index: 999;

    @media(max-width: 1200px) {
        .navigation {
            display: none;
        }
    }

    .drop-list {
        transition: 0.2s ease-in-out 0.1s;
        background-color: $color_header_bgc;

        ul {
            background-color: $color_header_bgc_select;
        }

        &:hover {
            background-color: $color_header_bgc_select;
        }

        li a:hover {
            box-shadow: inset 0 0 10px rgba(255, 255, 255, 0.106);
        }
    }

    a,
    .drop-list span {
        display: flex;
        align-items: center;
        padding: min(10px) 20px;
        height: 100%;
        color: white;
        font-weight: 300;

        &:hover {
            background-color: $color_header_bgc_select;
            color: white;
        }
    }

    nav {
        display: flex;
        align-items: center;
        justify-content: space-between;
        height: 100%;

        >ul {
            display: flex;
            flex-grow: 1;
            align-items: center;
            justify-content: start;
            height: 100%;
            background-color: $color_header_bgc;

            li {
                height: 100%;

            }
        }
    }

    .authorization {
        display: flex;
        // flex-grow: 1;
        align-items: center;
        justify-content: end;
        width: max-content + 10px;
        height: 100%;
        border-left: 1px solid $color_header_bgc_select;

        @media(max-width:1200px) {
            border-left: none;

        }

        a {
            justify-content: end;
            width: 100%;
        }

        span {
            margin-left: 10px;
        }
    }

    .burger {
        display: none;
        margin-right: 10px;
        padding: 8px;

        &:hover {
            background-color: rgba(255, 255, 255, 0.709);
            border-radius: 5px;
            .lines>*{
                background-color: rgb(0, 142, 0);
            }
        }

        &.open {
            .line_2 {
                background-color: transparent;
            }

            .line_1 {
                transform-origin: 0 3px;
                transform: rotateZ(29deg);
                width: 32px;
            }

            .line_3 {
                transform-origin: 0 0px;
                transform: rotateZ(-29deg);
                width: 32px;
            }
        }

        .lines>* {
            width: 25px;
            height: 3px;
            background-color: rgb(0, 189, 0);
            border-radius: 3px;

            transition-duration: 0.5s;
            transition-timing-function: ease-in;

            &:not(:first-child) {
                margin-top: 5px;
            }

        }




        @media(max-width:1200px) {
            display: block;

        }
    }
}