._bread_crumbs {
    font-size: 18px;
    color: rgba(0, 0, 0, 0.5);

    &>.wrapper {
        display: flex;
        justify-content: center;
        text-align: center;
        margin: 45px auto 0;

        &>:last-child {
            font-weight: 700;
            color: rgba(0, 0, 0, 0.5);
            user-select: none;
        }
        &>:not(:last-child):after{
            content:"";
            display: inline-block;
            margin: 0 5px;
            width: 6px;
            height: 6px;
            border-radius: 50%;
            background-color:#84C551;
            vertical-align: 1px;
        }
    }
}