.about_us {
    position: relative;
    margin-top: 110px;
    font-size: 16px;

    ._conteiner-big {
        padding: 0 20px;
    }

    .strong {
        font-weight: 700;
    }

    .green {
        color: #1E6140;
    }

    ._text_block_template {
        position: relative;
        margin-left: auto;
        width: 540px;

        @media(max-width:1200px) {
            width: 90vw;
        }

        ._leaf_title_decor {
            position: relative;

            @media(max-width:1200px) {
                font-size: 22px;
            }

            &::before {
                content: "";
                display: inline-block;
                margin-right: 10px;
                width: 32px;
                height: 25px;
                background-image: url("../img/icon/icon_leaves_vector/leaves_green_horisontal.png");
                background-size: 100% 100%;

                @media(max-width:1200px) {
                    margin-right: 5px;
                    width: 9px;
                    height: 9px;
                }
            }
        }

        .text_block__title {
            font-size: 45px;
            font-weight: 700;
            line-height: 67px;
            @media(max-width:1200px) {
                margin-top: 24px;
                margin-bottom: 10px;
                line-height: 1;
                }

            .thin {
                font-size: 45px;
                font-weight: 300;
            }
        }

        .text_block__wrapper {
            line-height: 20px;
            font-size: 18px;
            color: rgba(0, 0, 0, 0.5);
            @media(max-width:1200px) {
                    font-size: 16px;
                }

            .text_block__p:not(:first-child) {
                margin-top: 10px;
            }
        }

        .text_block__link {
            margin-top: 25px;
            @media(max-width:1200px) {
                    margin: 25px auto;
                    width: 90vw;
                    height: 7vw;
                }
        }
    }

    .fermer_frame {
        position: absolute;
        top: 0;
        z-index: -1;

        @media(max-width:1200px) {
            position: relative;

            img {
                width: 90vw;
            }

            .leafs {
                display: none;
            }
        }

        .leafs {
            :nth-child(1) {
                position: absolute;
                bottom: 130px;
                left: -50px;
            }

            :nth-child(2) {
                position: absolute;
                bottom: 0px;
                left: 150px;
            }
        }
    }
}